* {
    margin: 0;
    padding: 0; }

@font-face {
    font-family: 'Gilroy';
    src: url("../fonts/Gilroy-Semibold.ttf"); }

@font-face {
    font-family: 'Merriweather';
    src: url("../fonts/Merriweather-Black.ttf"); }

body {
    background-color: #191411;
    font-family: 'Gilroy', sans-serif;
    color: white; }

.container {
    max-width: 1200px;
    margin: 0 auto; }


.button {
    background: linear-gradient(96deg, #DA8023 0%, #E2B438 100%);
    border-radius: 8px;
    width: 260px;
    height: 62px;
    padding: 19px;
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 0;
    cursor: pointer;
    outline: 0;
    font-size: 18px;
    color: #191411; }

.button:hover {
    background: linear-gradient(96deg, #ac6115 0%, #9e7a19 100%); }

.common-title {
    text-align: center;
    font-size: 64px;
    line-height: 80px;
    font-family: 'Merriweather', sans-serif;
    text-transform: uppercase;
    letter-spacing: 1.92px; }

.main {
    background-image: url("../img/main_bg.png");
    background-position: top center;
    overflow: hidden; }

.header {
    padding: 40px 0; }

.header .container {
    display: flex;
    align-items: center; }

.logo img {
    vertical-align: bottom; }

.menu {
    margin-left: 191px; }

.menu-list {
    list-style: none;
    display: flex; }

.menu-item {
    margin-right: 88px; }

.menu-item a {
    color: white;
    font-style: 18px;
    text-decoration: none;
    cursor: pointer; }

.menu-item a:hover {
    border-bottom: 2px solid white; }

.currency {
    border: 1px solid white;
    padding: 11px 0;
    box-sizing: border-box;
    border-radius: 5px;
    width: 45px;
    height: 45px;
    text-align: center;
    cursor: pointer;
    margin-left: auto;
    font-size: 18px;
    user-select: none; }

.main-content {
    padding-top: 103px;
    padding-bottom: 199px; }

.main-content .container {
    position: relative; }

.main-info {
    max-width: 608px; }

.main-small-info {
    background: #d3320f;
    border-radius: 100px;
    padding: 12px 16px;
    display: inline-block;
    font-size: 18px; }

.main-title {
    font-family: 'Merriweather', sans-serif;
    font-size: 120px;
    line-height: 156px;
    letter-spacing: 3.60px;
    text-transform: {}
    margin-bottom: 56px;
    text-transform: uppercase; }

.main-text {
    font-size: 24px;
    line-height: 29px;
    margin-bottom: 56px; }

.main-img {
    position: absolute;
    top: -123px;
    left: calc(100% - 991px); }

.why {
    background-image: url("../img/whybg.png");
    background-position: top center;
    background-size: initial;
    padding-bottom: 180px; }


.why-items {
    margin-top: 60px;
    display: grid;
    gap: 100px;
    grid-template-columns: repeat(3, 312px);
    justify-content: center; }

.why-item {
    text-align: center; }

.why-item-title {
    font-size: 24px;
    line-height: 29px;
    padding: 24px 0; }

.why-item-text {
    font-size: 16px;
    line-height: 140%; }

.products {
    background-image: url("../img/burgers_bg.png");
    background-position: top center;
    background-size: 1400px;
    padding-bottom: 180px; }

.products-items {
    display: grid;
    grid-template-columns: repeat(3, 384px);
    gap: 24px;
    margin-top: 60px; }

.products-item {
    padding: 30px;
    background: #211a16;
    border-radius: 16px;
    border: 1px #353535 solid; }

.products-item-img {
    height: 250px; }

.products-item-img img {
    max-width: 100%; }

.products-item-title {
    font-size: 24px;
    line-height: 29px;
    margin: 24px 0; }

.products-item-text {
    font-size: 16px;
    line-height: 140%;
    margin-bottom: 24px; }

.products-item-extra {
    display: flex;
    justify-content: space-between; }

.products-item-price {
    font-size: 32px;
    line-height: 39px;
    margin-bottom: 8px; }

.products-item-weight {
    font-size: 16px;
    line-height: 18px;
    color: #757575; }

.button.product-button {
    display: flex;
    justify-content: center;
    width: 182px;
    height: 62px; }

.button.product-button span:first-child {
    margin-right: 10px; }

.order {
    background-image: url("../img/order_bg.png");
    background-position: top center;
    background-repeat: no-repeat;
    padding-bottom: 180px;
    overflow: hidden; }

.order .container {
    position: relative; }

.order-img {
    position: absolute;
    top: 0;
    right: calc(100% - 764px);
    z-index: -1; }

.order-form {
    background: #211A16;
    border-radius: 16px;
    border: 1px #353535 solid;
    margin-top: 60px;
    width: 426px;
    padding: 60px 40px;
    box-sizing: border-box;
    margin-left: 674px; }

.order-form-text {
    line-height: 29px;
    font-size: 24px;
    text-align: center; }

.order-form-inputs {
    display: flex;
    flex-direction: column;
    margin-top: 40px; }

.order-form-input {
    width: 344px;
    height: 62px;
    background: linear-gradient(95.61deg, #DA8023 0%, #e2b438 100%);
    border-radius: 8px;
    margin-bottom: 20px;
    display: flex;
    align-items: center;
    justify-content: center; }

.order-form-input input {
    padding: 16px 10px;
    background: #211a16;
    border-radius: 8px;
    color: white;
    width: 342px;
    height: 60px;
    box-sizing: border-box;
    outline: none;
    border: 1px solid transparent; }

.order-form-inputs .button {
    width: 344px;
    height: 62px; }

.footer {
    padding: 60px 0; }

.footer .container {
    display: flex;
    align-items: center;
    justify-content: space-between; }

.rights {
    font-size: 16px;
    line-height: 20px; }
